import React, { useState } from "react";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import * as Yup from "yup";

import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import Box from "@mui/material/Box";
import FormControl from "@mui/material/FormControl";
import FormHelperText from "@mui/material/FormHelperText";
import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";
import InputAdornment from "@mui/material/InputAdornment";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import { useTheme } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";

import { Formik } from "formik";


import {
  postSignup,
  postUserExist,
} from "../../../store/slice/auth/signup/signupSlice";
import translate from "../../hooks/translate";
import CMLoadingButton from "../../ui-components/buttons/CMLoadingButton";

const RegisterForm = () => {
  const [showPassword, setShowPassword] = useState(false);
  const loading = useSelector((state) => state.signup.loading);
  const { executeRecaptcha } = useGoogleReCaptcha();

  const theme = useTheme();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  
  const specialCharacter = /^[aA-zZ\s]+$/;
  const schemaObj = Yup.object().shape({
    firstName: Yup.string()
      .min(3, translate("common_error_too_short_min_3_character_required"))
      .max(18, translate("common_error_too_long_max_18_character_required"))
      .matches(
        specialCharacter,
        translate("common_error_special_characters_is_not_allowed")
      )
      .required(translate("consultant_first_name_required")),
    lastName: Yup.string()
      .min(3, translate("common_error_too_short_min_3_character_required"))
      .max(18, translate("common_error_too_long_max_18_character_required"))
      .matches(
        specialCharacter,
        translate("common_error_special_characters_is_not_allowed")
      )
      .required(translate("consultant_last_name_required")),
    email: Yup.string()
      .email(translate("common_error_email_valid_email"))
      .required(translate("common_error_email_required")),
    password: Yup.string()
      .min(6, translate("common_error_password_too_short_min_6_length"))
      .max(18, translate("common_error_password_too_long_max_18_length"))
      .required(translate("common_error_password_required")),
  });

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handelSignup = async (data) => {
    if (!executeRecaptcha) {
      toast.error("You are not Authorized");
      return;
    }
    const token = await executeRecaptcha("yourAction");
    const signupPayload = {
      firstName: data.firstName,
      lastName: data.lastName,
      email: data.email,
      password: data.password,
      googleRecaptchaToken: token,
    };

    const res = await dispatch(postSignup(signupPayload))
      .unwrap()
      .then((result) => {
        if (result.status) {
          toast.success(result.message);
          navigate("/otp-verify", { state: { isOtpVerified: true } });
        } else {
          toast.error(result.message);
        }
      })
      .catch((error) => {
        console.log("signup error========>", error);
      });
    return res;
  };

  const handleUserExist = async (value, setFieldError) => {
    if (value) {
      const payload = {
        email: value,
      };
      const res = await dispatch(postUserExist(payload))
        .unwrap()
        .then((result) => {
          if (result.status === false) {
            const error = "email is already register !";
            setFieldError("email", error);
          }
        })
        .catch((error) => {
          console.log("user verify error========>", error);
        });
      return res;
    }
  };

  return (
    <>
      <Box component="div" className="h-100">
        <Formik
          initialValues={{
            firstName: "",
            lastName: "",
            email: "",
            password: "",
          }}
          validationSchema={schemaObj}
          onSubmit={(values) => {
            handelSignup(values);
          }}
        >
          {(props) => (
            <form onSubmit={props.handleSubmit}>
              <Grid container>
                <Grid
                  item
                  sm={9}
                  md={10}
                  lg={8}
                  sx={{ mx: { md: "0", xs: "auto" } }}
                >
                  <Typography variant="h4" align="center" mb={2}>
                    {translate("common_register_now")}
                  </Typography>
                  <Typography variant="body1" color="text.secondary" align="center" my={2}>
                    {translate("consultant_sign_up_with_email_address")}
                  </Typography>
                  <Grid container spacing={2} justifyContent="center">
                    <Grid item xs={6}>
                      <TextField
                        label={translate("common_first_name")}
                        name="firstName"
                        value={props.values.firstName}
                        onChange={props.handleChange}
                        onBlur={props.handleBlur}
                        error={
                          props.errors.firstName && props.touched.firstName
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.firstName && props.touched.firstName
                            ? props.errors.firstName
                            : null
                        }
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        label={translate("common_last_name")}
                        name="lastName"
                        value={props.values.lastName}
                        onChange={props.handleChange}
                        onBlur={props.handleBlur}
                        error={
                          props.errors.lastName && props.touched.lastName
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.lastName && props.touched.lastName
                            ? props.errors.lastName
                            : null
                        }
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        label={translate("common_email")}
                        name="email"
                        value={props.values.email}
                        onChange={props.handleChange}
                        onBlur={(e) => {
                          handleUserExist(
                            props.values.email,
                            props.setFieldError,
                            props.errors
                          );
                          props.handleBlur(e);
                        }}
                        error={
                          props.errors.email && props.touched.email
                            ? true
                            : false
                        }
                        helperText={
                          props.errors.email && props.touched.email
                            ? props.errors.email
                            : null
                        }
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <FormControl
                        fullWidth
                        sx={{ ...theme.typography.customInput }}
                      >
                        <InputLabel
                          error={
                            props.errors.password && props.touched.password
                          }
                          htmlFor="outlined-adornment-password"
                        >
                          {translate("common_password")}
                        </InputLabel>
                        <OutlinedInput
                          label={translate("common_password")}
                          type={showPassword ? "text" : "password"}
                          name="password"
                          value={props.values.password}
                          onChange={props.handleChange}
                          onBlur={props.handleBlur}
                          error={
                            props.errors.password && props.touched.password
                              ? true
                              : false
                          }
                          endAdornment={
                            <InputAdornment position="end">
                              <IconButton
                                onClick={handleClickShowPassword}
                                onMouseDown={handleMouseDownPassword}
                                edge="end"
                              >
                                {showPassword ? (
                                  <VisibilityOff />
                                ) : (
                                  <Visibility />
                                )}
                              </IconButton>
                            </InputAdornment>
                          }
                        />
                        {props.errors.password && props.touched.password && (
                          <FormHelperText error>
                            {props.errors.password}
                          </FormHelperText>
                        )}
                      </FormControl>
                    </Grid>
                    <Grid item xs={12}>
                      <CMLoadingButton
                        variant="contained"
                        fullWidth
                        type="submit"
                        loading={props.isSubmitting && loading}
                        disabled={props.isSubmitting && loading}
                      >
                        {translate("common_sign_up")}
                      </CMLoadingButton>
                    </Grid>
                    <Typography variant="body1" my={2}>
                      {translate("common_already_have_an_account")} ?{" "}
                      <Link to="/login">{translate("common_login")}</Link>
                    </Typography>
                  </Grid>
                </Grid>
              </Grid>
            </form>
          )}
        </Formik>
      </Box>
    </>
  );
};

export default RegisterForm;
